import { useEffect, useRef } from "react";
import styled from "styled-components";
import { SandboxContext } from "../context";
import { useSandboxState } from "../hooks";
import Menu from "../Menu";
import Sandbox from "../Sandbox";
import { saveAs } from "file-saver";
import "./App.css";

const StyledApp = styled.div`
  background-color: #0c192c;
  display: flex;
  height: 100vh;
  width: 100vw;
  position: relative;
`;

const StyledButtons = styled.div`
  position: absolute;
  display: flex;
  gap: 5px;
  right: 0;
`;

function App() {
  const sandboxState = useSandboxState();
  const setMode = sandboxState.setMode;

  useEffect(() => {
    const handleKey = e => {
      if (e.keyCode === 49) {
        setMode("create");
      } else if (e.keyCode === 50) {
        setMode("delete");
      }
    };
    document.addEventListener("keypress", handleKey);
    return () => {
      document.removeEventListener("keypress", handleKey);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const saveState = () => {
    const blob = new Blob([sandboxState.getJSONState()]);
    saveAs(blob, "SandboxSave.json");
  };
  const ref = useRef(null);
  const loadFile = () => {
    ref.current.click();
  };
  const handleFile = e => {
    const file = ref.current.files[0];
    const reader = new FileReader();
    reader.readAsText(file, "UTF-8");
    reader.onload = function (evt) {
      sandboxState.loadState(evt.target.result);
    };
  };
  return (
    <SandboxContext.Provider value={sandboxState}>
      <StyledApp>
        <Menu />
        <Sandbox />
        <StyledButtons>
          <button onClick={saveState}></button>
          <button onClick={loadFile}></button>
          <input
            style={{ display: "none" }}
            ref={ref}
            onChange={e => handleFile(e)}
            type="file"
          />
        </StyledButtons>
      </StyledApp>
    </SandboxContext.Provider>
  );
}

export default App;
