import React from "react";

const Cube = ({
  position,
  preview,
  onHoverMesh,
  onCreate,
  toDelete,
  onHoverGroup,
  idx,
  onDelete,
  color = "#ffa500",
}) => {
  const size = 0.5;
  const planeSize = [size, size];
  const opacity = preview ? 0.7 : 1;
  if (toDelete) color = "red";
  const setPreview = (e, position) => {
    if (!onHoverMesh) return;
    onHoverMesh(e, position);
  };
  const setDelete = (e, idx) => {
    if (!onHoverGroup) return;
    onHoverGroup(e, idx);
  };
  const handleClick = e => {
    if (preview && onCreate) return onCreate(e);
    if (!preview && toDelete) return onDelete(e);
  };
  return (
    <group
      position={position}
      onClick={handleClick}
      onPointerMove={e => setDelete(e, idx)}
      onPointerLeave={e => setDelete(e, null)}
    >
      {/*  */}
      <mesh
        position={[0, 0, size / 2]}
        onPointerMove={e =>
          setPreview(e, [position[0], position[1], position[2] + size])
        }
        onPointerLeave={e => setPreview(e, null)}
      >
        <planeGeometry args={planeSize} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
      {/*  */}
      <mesh
        position={[0, 0, 0 - size / 2]}
        rotation={[0, Math.PI, 0]}
        onPointerMove={e =>
          setPreview(e, [position[0], position[1], position[2] - size])
        }
        onPointerLeave={e => setPreview(e, null)}
      >
        <planeGeometry args={planeSize} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
      {/*    */}
      <mesh
        position={[-size / 2, 0, 0]}
        rotation={[0, -Math.PI / 2, 0]}
        onPointerMove={e =>
          setPreview(e, [position[0] - size, position[1], position[2]])
        }
        onPointerLeave={e => setPreview(e, null)}
      >
        <planeGeometry args={planeSize} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
      {/*    */}
      <mesh
        position={[size / 2, 0, 0]}
        rotation={[0, Math.PI / 2, 0]}
        onPointerMove={e =>
          setPreview(e, [position[0] + size, position[1], position[2]])
        }
        onPointerLeave={e => setPreview(e, null)}
      >
        <planeGeometry args={planeSize} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
      {/*  */}
      <mesh
        position={[0, -size / 2, 0]}
        rotation={[Math.PI / 2, 0, 0]}
        onPointerMove={e =>
          setPreview(e, [position[0], position[1] - size, position[2]])
        }
        onPointerLeave={e => setPreview(e, null)}
      >
        <planeGeometry args={planeSize} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
      {/*  */}
      <mesh
        position={[0, size / 2, 0]}
        rotation={[-Math.PI / 2, 0, 0]}
        onPointerMove={e =>
          setPreview(e, [position[0], position[1] + size, position[2]])
        }
        onPointerLeave={e => setPreview(e, null)}
      >
        <planeGeometry args={planeSize} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
      <mesh position={[0, size / 2 + size / 10, 0]}>
        <cylinderGeometry args={[size / 3, size / 3, size / 5, 16]} />
        <meshStandardMaterial
          color={color}
          transparent={!!preview}
          opacity={opacity}
        />
      </mesh>
    </group>
  );
};

export default Cube;
