import { useEffect, useState } from "react";

export const useSandboxState = () => {
  const initialColor = "#ffa500";
  const initState = {
    mode: "create",
    colors: [initialColor],
    selectedColor: 0,
  };
  const [sandboxState, setSandboxState] = useState(initState);
  const [boxes, setBoxes] = useState([
    { position: [0, 0, 0], color: initState.colors[initState.selectedColor] },
  ]);

  useEffect(() => {
    if (!sandboxState.colors[sandboxState.selectedColor])
      setSandboxState(oldValue => {
        return { ...oldValue, selectedColor: 0 };
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sandboxState.colors]);

  const setMode = mode => {
    setSandboxState(oldValue => {
      return { ...oldValue, mode };
    });
  };

  const setColor = (idx, color) => {
    setSandboxState(oldValue => {
      const newColors = [...oldValue.colors];
      newColors[idx] = color;
      return { ...oldValue, colors: newColors };
    });
  };

  const addColor = () => {
    setSandboxState(oldValue => {
      return { ...oldValue, colors: [...oldValue.colors, initialColor] };
    });
  };

  const selectColor = idx => {
    setSandboxState(oldValue => {
      return { ...oldValue, selectedColor: idx };
    });
  };

  const getJSONState = () => {
    return JSON.stringify({ sandboxState, boxes });
  };

  const loadState = json => {
    let result = null;
    try {
      result = JSON.parse(json);
    } catch (error) {
      alert(" ");
    }
    if (result && result.boxes && result.sandboxState) {
      setSandboxState(result.sandboxState);
      setBoxes(result.boxes);
    } else {
      alert(" ");
    }
  };

  return {
    sandboxState,
    setMode,
    setColor,
    addColor,
    selectColor,
    boxes,
    setBoxes,
    getJSONState,
    loadState,
  };
};

export const useOnClickOutside = (ref, handler) => {
  useEffect(() => {
    const listener = event => {
      if (!ref.current || ref.current.contains(event.target)) {
        return;
      }
      handler(event);
    };
    document.addEventListener("mousedown", listener);
    return () => {
      document.removeEventListener("mousedown", listener);
    };
  }, [ref, handler]);
};
