import { Canvas } from "@react-three/fiber";
import React, { useContext, useEffect, useState } from "react";
import { OrbitControls, PerspectiveCamera } from "@react-three/drei";
import Cube from "../Cube";
import { SandboxContext } from "../context";
import styled from "styled-components";

const CanvasWrapper = styled.div`
  width: 100%;
  height: 100%;
`;

const Sandbox = () => {
  const {
    sandboxState: { colors, selectedColor, mode },
    boxes,
    setBoxes,
  } = useContext(SandboxContext);
  const initPreview = { event: null, position: null };
  const [preview, setPreview] = useState(initPreview);
  const initDelete = { event: null, idx: null };
  const [toDelete, setToDelete] = useState(initDelete);

  useEffect(() => {
    if (mode === "delete") {
      setPreview(initPreview);
    }
    if (mode === "create") {
      setToDelete(initDelete);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mode]);

  const setPreviewItem = (e, position) => {
    if (mode !== "create") return;
    setPreview({ event: e, position });
    return e.stopPropagation();
  };

  const setDeleteItem = (e, idx) => {
    if (mode !== "delete") return;
    setToDelete({ event: e, idx });
    return e.stopPropagation();
  };

  const createBox = e => {
    if (mode !== "create") return;
    e.stopPropagation();
    if (preview?.position) {
      setBoxes(oldValue => {
        return [
          ...oldValue,
          { position: preview.position, color: colors[selectedColor] },
        ];
      });
      setPreview(initPreview);
    }
  };

  const deleteBox = e => {
    if (mode !== "delete") return;
    e.stopPropagation();
    if (toDelete?.idx !== null) {
      setBoxes(oldValue => {
        const newValue = oldValue.filter((item, idx) => {
          return idx !== toDelete.idx;
        });
        return newValue;
      });
      setToDelete(initDelete);
    }
  };

  return (
    <CanvasWrapper>
      <Canvas>
        <ambientLight intensity={0.3} />
        <spotLight position={[6, 6, 11]} angle={0.4} penumbra={1} />
        <pointLight position={[-6, -6, -11]} />
        <PerspectiveCamera position={[6, 6, 10]} makeDefault />
        <OrbitControls />
        {boxes.map(({ position, color }, idx) => {
          return (
            <Cube
              key={position.join(";")}
              onHoverGroup={setDeleteItem}
              onHoverMesh={setPreviewItem}
              position={position}
              idx={idx}
              toDelete={toDelete.idx === idx}
              onDelete={deleteBox}
              color={color}
            />
          );
        })}
        {preview?.position ? (
          <Cube
            position={preview.position}
            preview
            onCreate={createBox}
            color={colors[selectedColor]}
          />
        ) : null}
      </Canvas>
    </CanvasWrapper>
  );
};

export default Sandbox;
